.App {
  text-align: center;
}

.app-logo {
  height: 30vmin;
  border-radius: 15vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .app-logo {
    animation: App-logo-spin infinite 0.5s linear;
  }
}

.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-header button {
  padding: 1em 2em;
  font-family: sans-serif;
  font-weight: bold;
  color: whitesmoke;
  background-color: blue;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}

p {
  color: white;
  padding: 40px;
}

.links {
  display: flex;
  gap: 10px;
  padding: 1em;
}

.links a {
  cursor: pointer;
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
